$(function() {
    $(document).on('click', '.js-anchor', function (event) {
        event.preventDefault();
        $('body').css('overflow', 'visible');
        
        $('html, body').stop(true, true).delay(200).animate({
            scrollTop: $($.attr(this, 'href')).offset().top
        }, 500);   
    });

    $('.js-modal-close').on('click', function(){
        $.fancybox.close()
    })
})