$(function() {
    $('.js-open-models').on('click', function() {
        const models = JSON.parse($(this).attr('data-models'));
        const name = $(this).attr('data-name');
        const logo = $(this).attr('data-logo');
        const category = $(this).attr('data-category');

        if(models) {
             $.fancybox.open(`
                <div class="brand__popup">
                    <div class="brand__popup-top">
                        <div class="brand__popup-left">
                            <span class="brand__popup-name">${name}</span>
                            <span class="brand__popup-category">${category}</span>
                        </div>
                        <div class="brand__popup-logo style="">
                            <img src="./img/content/brands/${logo}.png">
                        </div>
                    </div> 
                    <ul class="brand__popup-list">
                        ${models.map(item => `
                            <li>
                                <span class="brand__popup-model">${item.name}</span>
                                <span class="brand__popup-cost">${item.cost} ₽</span>
                            </li>`
                        ).join('')}
                    </ul>
                    <button class="brand__popup-btn btn btn_orange" data-src="#sign-up" data-fancybox >Записаться</button>
                </div>
            `);

            return
        }

        $.fancybox.open(`
            <div class="brand__popup">
                <div class="brand__popup-top">
                    <div class="brand__popup-left">
                        <span class="brand__popup-name">${name}</span>
                        <span class="brand__popup-category">${category}</span>
                    </div>
                    <div class="brand__popup-logo style="">
                        <img src="./img/content/brands/${logo}.png">
                    </div>
                </div> 
                <h3>Извините, модели отсутствуют</h3>
                <button class="brand__popup-btn btn btn_orange" data-src="#sign-up" data-fancybox >Записаться</button>
            </div>
        `)
    });
    
});