$(function() {
    const $form = $('.js-form');

    $("[type='tel']").mask("+7 (999) 999-9999", {
        completed: function completed() {
            $(this).parent().removeClass('error')
        }
    });

    $('.js-form-select').select2({
    })

    $form.find('#name').on('input', function() {
        const value = $(this).val()
        if(value.length > 3) $(this).parent().removeClass('error')
    })

    $form.find('.js-checkbox').on('change', function() {
        $(this).next().removeClass('error')
    })

    $form.on('submit', function(e) {
        e.preventDefault()
        const $this = $(this)
        const url = $this.attr('data-action');
        const name = $this.find('#name')
        const phone = $this.find('#phone');
        const model = $this.find('#model');
        const brand = $this.find('#brand');
        const submit = $this.find('.js-form__btn')

        if(name.val() === '' || name.val().length < 3) name.parent().addClass('error');
        if(phone.val() === '') phone.parent().addClass('error');
        if(name.val().length < 3 || phone.val() === '') return;

        submit.attr('disabled', true);

        const modelVal = model.val();
        const brandVal = brand.val();
        const nameVal = name.val();
        const phoneVal = phone.val().replace(/\D+/g, '');

        $.post(url, {
            brand: brandVal,
            model: modelVal,
            name: nameVal, 
            phone: phoneVal 
        })
        .done(function (response) {
            submit.removeAttr('disabled');
            $form.each(i=> $form[i].reset())
            $.fancybox.close();
            $.fancybox.open({ 
                src: "#success", 
                type : 'inline',
                opts : {
                    afterShow : function( instance, current ) {
                        setTimeout(()=> $.fancybox.close(), 3000)
                    }
                }
            });
        }).fail(function(response) {
            
        })

    })
})